import React from 'react'
import styled from 'styled-components'
import ReactLogo from "../images/react.svg";
import AwsLogo from "../images/aws.svg";
import SpringLogo from "../images/spring.png";

const ToolboxContainer = styled.div`
    margin: auto;
    border-bottom: 1px solid #ccc;
    padding-bottom: 50px;
    margin-bottom: 20px;
`;

const ToolsList = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    
    div {
      width: calc(100% / 3);
      text-align: center;
      
      img {
        max-width: 100%;
        max-height: 85px;
        display: block;
        margin: auto;
      }
    }
    
    img{
      padding-bottom: 10px;
      //border-bottom: 5px solid transparent;
      transition: all .1s ease-in-out;
      &:hover {
        transform: scale(1.1);
        padding-bottom: 10px;
        //border-bottom: 5px solid #68bd45;
    }
`;

const Toolbox = () => (
    <div>
        <h3>Area of expertise</h3>

        <ToolboxContainer>
            <ToolsList>
                <div><a href="https://spring.io"><img src={SpringLogo} alt="Spring Framework"/></a></div>
                <div><a href="https://reactjs.org/"><img src={ReactLogo} alt="React"/></a></div>
                <div><a href="https://aws.amazon.com/"><img src={AwsLogo} alt="Amazon Web Services"/></a></div>
            </ToolsList>
        </ToolboxContainer>
    </div>
);

export default Toolbox;


